import React from 'react';
import { routerHistory } from '../consts';

export class ScrollTopService {
  constructor(private scrollRef: React.MutableRefObject<HTMLElement | null>) {}

  init(): void {
    routerHistory.listen(() => {
      if (this.scrollRef.current) {
        this.scrollRef.current.scrollTo(0, 0);
      }
    });
  }
}
