import { allProjectsService, useRx } from 'common';
import { Text } from 'components';
import React from 'react';

type THashtagProps = {
  hashtag: string;
  isActive: boolean;
  onClick: (hashtag: string) => void;
} & THashtagSheetProps;

type THashtagSheetProps = {
  scrollableRef: React.MutableRefObject<HTMLElement | null>;
};

function Hashtag({ hashtag, isActive, onClick, scrollableRef }: THashtagProps): JSX.Element {
  const handleClick = React.useCallback(() => {
    onClick(hashtag);

    if (scrollableRef.current) {
      scrollableRef.current.scrollTo(0, 0);
    }
  }, [onClick, hashtag, scrollableRef]);

  return (
    <Text align="center" onClick={handleClick} size="l" color={isActive ? 'red' : 'white'}>{hashtag}</Text>
  );
}


export function HashtagsSheet({ scrollableRef }: THashtagSheetProps): JSX.Element {
  const hastags = useRx(allProjectsService.hashtags$);
  const currentHashtag = useRx(allProjectsService.currentHashTag$);

  return (
    <>
      {hastags.map((hashtag: string) => (
        <Hashtag
          scrollableRef={scrollableRef}
          key={hashtag}
          onClick={allProjectsService.setHashtag}
          hashtag={hashtag}
          isActive={hashtag === currentHashtag}
        />
      ))}
    </>
  );
}
