import { ScrollTopService } from 'common';
import React from 'react';
import { footerStyle, layoutStyle } from './layout.style';
import { Colors } from '../colors';
import { CopyRight } from '../copyright';

type TLayoutProps = {
  children: React.ReactNode;
  isContentFullLayoutView?: boolean;
  bgrColor?: Colors;
  noFooter?: boolean;
  scrollableElementRef?: React.MutableRefObject<HTMLElement | null>;
};

export function Layout({
  children,
  bgrColor,
  isContentFullLayoutView = true,
  noFooter = true,
  scrollableElementRef
}: TLayoutProps): JSX.Element {
  const ref = React.useRef<HTMLElement | null>(null);
  const actualRef = scrollableElementRef || ref;
  const [scrollService] = React.useState(() => new ScrollTopService(actualRef));

  React.useEffect(() => {
    document.body.style.backgroundColor = bgrColor || Colors.BLACK;
  }, [bgrColor]);

  React.useEffect(() => {
    scrollService.init();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <>
      <main
        className={layoutStyle(isContentFullLayoutView, noFooter)}
        ref={actualRef}
      >
        {children}
      </main>
      {!noFooter && (
        <footer className={footerStyle}>
          <CopyRight />
        </footer>
      )}
    </>
  );
}
